* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(175, 228, 207);
}

.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  height: 50vh;
  width: 50vw;
  align-items: center;
  padding: 1rem;
}

input,
button,
select {
  height: 30px;
  border: none;
  font-size: 15px;
}
input:focus,
button:focus,
select:focus {
  outline: none;
}

button {
  width: 2rem;
  background: none;
  background-color: rgb(248, 248, 248);
}
.form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}

i {
  background-color: rgb(252, 89, 89);
  padding: 2px;
  border-radius: 5px;
  filter: invert();
}

select {
  width: 200px;
}

ul {
  margin: 30px 0 0 0;
  text-align: center;
  list-style: none;
  display: grid;
  align-items: center;
  justify-content: center;
}

.todo-item {
  display: flex;
}
.todo-item > li {
  background-color: white;
  min-width: 25vw;
}

.todo-item > button > i {
  background: none;
}

.trash {
  background-color: red;
}

.check {
  background-color: gray;
}


.completed{
  color: aqua;
  text-decoration: line-through;
}
